<template>
    <div class="AnunciosBox">
        <div class="loader color-green" v-if="isLoading"></div>
        <div v-else>
            <div class="SearchBox" v-if="!inProduct">
                <img src="../assets/search.png" class="SearchImage">
                <input type="text" class="SearchInput" v-model="search" :placeholder="translate.TR_SEARCH">
            </div>

            <div class="ProductsBox" v-if="!inProduct">
                <!-- <p class="ButtonRefresh">Atualizar</p> -->
                <ul class="ProductsList">
                    <li class="ProductsItemBox"
                        v-for="(item,index) in products.filter((item) => item.name.toLowerCase().includes(search.toLowerCase()))"
                        :key="index"
                    >
                        <div class="clickOption" v-on:click="seeProduct(item)"></div>
                        <p class="ProductsItemName">{{item.name}} x{{item.amount}}</p>

                        <img :src="item.img" alt="" class="ProductsItemImage">
                        
                        <p class="ProductsItemOwner">{{translate.TR_BY_OWNER}} {{item.owner}}</p>

                        <p class="ProductsItemPrice">{{translate.TR_SIMBOL_MONEY}} {{item.price}}</p>
                    </li>
                </ul>
            </div>

            <div class="BuyBox" v-else>
                <div class="InfoProductBox">
                    <div class="InfoProductImageBox">
                        <img :src="product.img" alt="" class="InfoProductImage">
                    </div>

                    <div class="InfoProductTextBox">
                        <div class="InfoProductInTextBox">
                            <p class="InfoProductName">{{product.name}}</p>
                            <p class="InfoProductAmount">{{translate.TR_WEIGHT}} {{product.weight}}</p>
                            <p class="InfoProductAmount">{{translate.TR_DISPONIBLE}} {{product.amount}} {{translate.TR_UNITS}}</p>
                            <p class="InfoProductOwner">{{translate.TR_BY_OWNER}} {{product.owner}}</p>
                        </div>

                        <div class="InfoProductPriceBox">
                            <p class="InfoProductPrice">{{translate.TR_SIMBOL_MONEY}} {{product.price}} {{translate.TR_UNITS2}}</p>
                        </div>
                    </div>
                </div>

                <div class="NeedsBox">
                    <div class="NeedsInfoBox">
                        <div class="NeedsAmountBox">
                            <p class="NeedsInfoTextAmount">{{translate.TR_MODAL_AMOUNT}}</p>
                            <input type="number" min="0" v-model="amountBuy" class="NeedsInfoInputAmount">
                        </div>

                        <div class="NeedsPriceBox">
                            <p class="TotalPrice">{{translate.TR_TOTAL_PRICE}}</p>
                            <p class="ValueTotalPrice">{{translate.TR_SIMBOL_MONEY}} {{(amountBuy * product.price)}}</p>
                        </div>
                    </div>

                    <div class="NeedsButtonBox">
                        <p class="NeedsButtonBuy" v-on:click="buyItem">{{translate.TR_BUTTON_BUY}}</p>
                        <p class="NeedsButtonCancel" v-on:click="cancelBuy">{{translate.TR_BUTTON_CANCEL}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        products:{

        },
        translate:{

        },
        NameResource:{
            
        }
    },

    data: function(){
        return({
            search: "",

            inProduct: false,

            product: {

            },

            amountBuy: 1,
            isLoading: true,
        })
    },

    methods:{
        seeProduct: function(item){
            this.product = item;
            this.inProduct = true;
        },

        cancelBuy: function(){
            this.inProduct = false;
        },

        buyItem: function(){
            if(this.product.amount >= this.amountBuy && this.amountBuy > 0){
                fetch(`https://${this.NameResource}/buyItem`, {
                    method: 'POST',
                    body: JSON.stringify({
                        item: this.product,
                        selectAmount: this.amountBuy
                    })
                });

                this.inProduct = false;
                this.isLoading = true;
            }
            else{
                fetch(`https://${this.NameResource}/sendNotify`, {
                    method: 'POST',
                    body: JSON.stringify({
                       color: "brown",
                       Notify: this.translate.TR_DONT_AMOUNT
                    })
                }); 
            }
        },

        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.open){
                this.isLoading = false;
            }
            else if(lua.Refused){
                this.isLoading = false;
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receiveLua);  
        // window.addEventListener('keydown', this.keyPress);
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        // window.removeEventListener('keydown', this.keyPress);
    }
}
</script>

<style>
    .SearchBox{
        background-color: rgb(255, 255, 255);
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        height: 5vh;
        padding: 1vh;
    }
    .SearchImage{
        height: 100%;
        margin-right: 1vh;
    }

    .SearchInput{
        border: none;
        outline: none;

        color: #C8C8C8;
        font-size: 2vh;
    }
    .SearchInput::placeholder{
        color: #C8C8C8;
    }

    .ButtonRefresh{
        text-align: center;
        width: 12vh;

        margin: auto;

        background-color: #536175;
        padding: 1vh;

        font-size: 2vh;
        color: #E3E6EA;

        margin-top: 2vh;

        border-radius: 5px;
    }
    .ButtonRefresh:hover{
        background-color: #3b4554;
    }
    .ProductsBox{
        height: 55vh;
        overflow-y: auto;
    }
    .ProductsList{
        padding: 0vh 5vh;

        display: flex;
        flex-direction: row;
        flex-wrap: wrap;

        
    }

    .ProductsBox::-webkit-scrollbar-track {
        background-color: #ffffff;
    }
    .ProductsBox::-webkit-scrollbar {
        width: 0.2vh;
        background: rgba(80, 80, 80, 0.8);
    }
    .ProductsBox::-webkit-scrollbar-thumb {
        background: rgba(90, 90, 90, 0.8);
        border-radius: 20px;
    }

    .ProductsItemBox{
        /* display: inline-block; */
        background-color: rgb(255, 255, 255);

        margin-right: 1.5vh;
        margin-top: 2vh;

        width: 20vh;
        height: 15vh;

        border-radius: 10px;
        text-align: center;

        transition: 1s all;

        overflow: hidden;

        position: relative;
    }

    .ProductsItemBox:hover{
        /* background-color: rgb(233, 233, 233); */
        box-shadow: 1px 1px 10px 1px rgb(158, 158, 158);
    }

    .ProductsItemName{
        font-size: 1.4vh;
        font-weight: 500;

        color: #606060;

        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .ProductsItemImage{
        width: 35%;
        /* border-radius: 50%; */
        /* border: 1px solid rgba(128, 128, 128, 0.289); */
        margin-top: 0.5vh;
    }

    .ProductsItemOwner{
        font-size: 1.4vh;
        color: gray;

        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .ProductsItemPrice{
        font-size: 2vh;
        font-weight: bold;

        color: #0E8D00;

        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .BuyBox{
        background-color: white;        

        border-radius: 10px;
        overflow: hidden;

        width: 99%;
        height: 100%;
        margin: auto;
    }

    .InfoProductBox{
        display: flex;
        flex-direction: row;

        padding: 1vh;
        padding-bottom: 2vh;
        border-bottom: 1px solid rgba(128, 128, 128, 0.267);
    }

    .InfoProductImageBox{
        width: 15vh;
        height: 15vh;

        border: 1px solid rgba(128, 128, 128, 0.267);;
        border-radius: 10px;
    }

    .InfoProductImage{
        width: 100%;
    }

    .InfoProductTextBox{
        margin-left: 2vh;

        display: flex;
        flex-direction: column;

        justify-content: space-between;
    }

    .InfoProductPriceBox{
        
    }

    .InfoProductName{
        font-size: 3vh;

        color: #606060;
    }

    .InfoProductAmount{
        font-size: 1.6vh;

        color: #606060;
    }

    .InfoProductOwner{
        font-size: 1.6vh;

        color: #949494;
    }

    .InfoProductPrice{
        color: #606060;

        font-weight: bold;

        font-size: 3vh;
    }

    .NeedsAmountBox, .NeedsPriceBox{
        display: flex;
        flex-direction: row;

        padding: 1vh;
    }

    .NeedsInfoTextAmount, .TotalPrice{
        width: 17vh;
    }

    .NeedsInfoInputAmount{
        width: 8vh;
        border: none;
        outline: none;
    }

    .NeedsInfoTextAmount, .NeedsInfoInputAmount, .TotalPrice, .ValueTotalPrice{
        font-size: 2.5vh;

        color: #606060;
    }

    .NeedsButtonBox{
        display: flex;
        flex-direction: column;

        margin-top: 5vh;

        justify-content: center;
        align-items: center;
    }

    .NeedsButtonBuy{
        background-color: #0E8D00;
        color: #E3E6EA;
        font-size: 2.2vh;
        padding: 1vh;

        border-radius: 5px;
        margin-top: 5vh;

        width: 12vh;
        text-align: center;
    }

    .NeedsButtonBuy:hover{
        background-color: #126409;
    }

    .NeedsButtonCancel{
        background-color: #536175;
        color: #E3E6EA;
        font-size: 2.2vh;
        padding: 1vh;

        border-radius: 5px;

        margin-top: 2vh;
        margin-bottom: 50vh;

        width: 12vh;
        text-align: center;
    }

    .NeedsButtonCancel:hover{
        background-color: #3f444b;
    }
    
</style>