<template>
    <div id="App">
        <div class="Notify" :class="{NotifyOpenned: !NotifyOpenned}" :style="styleNotify">{{Notify}}</div>
        <div class="BodyBox" :class="{MarketOpenned : !openned}" :style="styleBox">
            <div class="HeaderBox">
                <p class="Title">{{translate.TR_TITLE}}</p>

                <p class="SubTitle">{{translate.TR_SUBTITLE}}</p>

                <p class="CloseTitle" v-on:click="close">X</p>
            </div>

            <div class="SectionBox">
                <div class="OptionsBox">
                    <p class="OptionsText">{{translate.TR_OPTIONS_TITLE}}</p>

                    <div class="ItemBox">
                        <div class="clickOption" v-on:click="openAnuncios"></div>
                        <div class="CircleGreen"></div>

                        <div class="ItemTextBox">
                            <p class="ItemTitle">{{translate.TR_OPTIONS_1}}</p>
                            <p class="ItemText">{{products.length}} {{translate.TR_ANNOUNCES}}</p>
                        </div>

                        <div class="ItemImageBox">
                            <img src="./assets/sale.png" alt="" class="ItemImage">
                        </div>
                    </div>

                    <div class="ItemBox">
                        <div class="clickOption" v-on:click="openMyAnuncios"></div>
                        <div class="CircleRed"></div>

                        <div class="ItemTextBox">
                            <p class="ItemTitle">{{translate.TR_OPTIONS_2}}</p>
                            <p class="ItemText">{{products.filter((item) => item.identifier == myIdentifier).length}} {{translate.TR_ANNOUNCES}}</p>
                        </div>

                        <div class="ItemImageBox">
                            <img src="./assets/announcement.png" alt="" class="ItemImage">
                        </div>
                    </div>
                </div>

                <div class="ContentBox">
                    <Anuncios v-if="page == 'Anuncios'" :products="products" :translate="translate" :NameResource="NameResource" />
                    <MyAnuncios v-else-if="page == 'MyAnuncios'" :myProducts="myProducts" :inventory="inventory" :translate="translate" :NameResource="NameResource" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import Anuncios from './screens/Anuncios';
import MyAnuncios from './screens/MyAnuncios';

export default {
    components:{
        Anuncios,
        MyAnuncios
    },
    data: function(){
        return({
            products:[
                {id: 1, name: "Mochila", owner: "zFightx", identifier: "123", amount: 10, price: 75, img: "/img/backpack.png"},
                {id: 1, name: "Mochila", owner: "zFightx", identifier: "", amount: 5, price: 75, img: "/img/backpack.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
                {id: 1, name: "Po caseiro", owner: "zFightx", identifier: "", amount: 150, price: 50, img: "/img/bread.png"},
            ],

            myProducts:[],
            inventory:[],

            styleBox: {
                left        : "50%",
                top         : "50%",
                transform   : "scale(1.0) translate(-50%, -50%)"
            },

            styleNotify:{
                backgroundColor: "green"
            },

            translate: {
                TR_TITLE        : "Market",
                TR_SUBTITLE     : "Coloque ou compre algo a venda",
                TR_OPTIONS_TITLE: "OPES MARKET",
                TR_OPTIONS_1    : "Anncios:",
                TR_OPTIONS_2    : "Meus anncios:",
                TR_ANNOUNCES    : "anncios",

                TR_SEARCH       : "Procurar por um produto",
                TR_BY_OWNER     : "Por:",
                TR_SIMBOL_MONEY : "R$ ",

                TR_WEIGHT       : "Peso:",
                TR_DISPONIBLE   : "Disponvel:",
                TR_UNITS        : "unidades",
                TR_UNITS2       : "a unidade",
                TR_TOTAL_PRICE  : "Preo Total:",

                TR_BUTTON_BUY       : "Comprar",
                TR_BUTTON_ANNOUNCE  : "Anunciar",
                TR_BUTTON_REMOVE    : "Remover",
                TR_BUTTON_CANCEL    : "Cancelar",

                TR_MODAL_TITLE      : "Anunciar Produto",
                TR_MODAL_ITEM       : "Item",
                TR_MODAL_AMOUNT     : "Quantidade",
                TR_MODAL_PRICE      : "Preo por unidade",
                TR_MODAL_ANONYMOUS  : "Anonymous",

                TR_DONT_FULL        : "Seu inventrio est muito cheio.",
                TR_DONT_MONEY       : "Voc no possui dinheiro suficiente.",
                TR_SUCESS           : "Compra realizada com sucesso",
                TR_REMOVED_ITEM     : "Item removido com sucesso",
                TR_DONT_AMOUNT      : "No h tantos itens assim a venda.",
                TR_NOT_FOUND        : "Item j vendido ou no encontrado.",
                TR_ADVERTISE_ITEM   : "Item anunciado com sucesso.",
                TR_DONT_AMOUNT2     : "Voc no possui essa quantidade.", 
            },

            myIdentifier    : "123",
            page            : "Anuncios",
            waitScreen      : false,
            NameResource    : "market",
            openned         : false,
            Notify          : "Voc comprou Mochila x10",
            NotifyOpenned   : false,
        })
    },

    methods:{
        openScreen: function(screen){
            if(!this.waitScreen){
                this.page = screen;
                this.waitScreen = true;

                setTimeout(this.freeScreen, 1000);  
            }          
        },

        openAnuncios: function(){
            this.openScreen('Anuncios');

            fetch(`https://${this.NameResource}/loadAnuncios`, {
                method: 'POST',
                body: JSON.stringify({})
            }); 
        },

        openMyAnuncios: function(){
            this.openScreen('MyAnuncios');

            fetch(`https://${this.NameResource}/loadMyAnuncios`, {
                method: 'POST',
                body: JSON.stringify({})
            }); 
        },

        freeScreen: function(){
            this.waitScreen = false;
        },

        disableNotify: function(){
            this.NotifyOpenned = false;
            this.Notify = "";
        },

        close: function(){
            fetch(`https://${this.NameResource}/close`, {
                method: 'POST',
                body: JSON.stringify({})
            }); 

            this.openned = false;
        },

        keyPress: function(event){
            const key = event.key;
            
            if(key == "Escape"){
                this.close();
            }
        },

        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.config){
                this.translate = lua.translate;

                this.styleBox.left = lua.config.left;
                this.styleBox.top = lua.config.top;
                this.styleBox.transform = `scale(${lua.config.size}) transform(-${lua.config.left}, -${lua.config.top})`;

                this.NameResource = lua.NameResource;
            }
            else if(lua.Notify){
                this.Notify = lua.Notify;
                this.styleNotify.backgroundColor = lua.color;
                this.NotifyOpenned = true;

                setTimeout(this.disableNotify, 5000);
            }
            else if(lua.open){
                this.products = lua.products;
                this.myIdentifier = lua.identifier;

                if(lua.products && lua.list_products){
                    for(let i = 0; i < this.products.length; i++){
                        for(let j = 0; j < lua.list_products.length; j++){
                            if(this.products[i].name == lua.list_products[j].label){
                                this.products[i].img = lua.list_products[j].img;
                            }
                        }
                    }
                }

                this.openned = true;
            }

            else if(lua.myProductsOpen){
                this.myProducts = lua.myProducts;
                this.inventory = lua.inventory;

                if(lua.myProducts && lua.list_products){
                    for(let i = 0; i < this.myProducts.length; i++){
                        for(let j = 0; j < lua.list_products.length; j++){
                            if(this.myProducts[i].name == lua.list_products[j].label){
                                this.myProducts[i].img = lua.list_products[j].img;
                            }
                        }
                    }

                    for(let i = 0; i < this.inventory.length; i++){
                        for(let j = 0; j < lua.list_products.length; j++){
                            if(this.inventory[i].name == lua.list_products[j].label){
                                this.inventory[i].price_recommended = lua.list_products[j].price_recommended;
                            }
                        }
                    }
                }
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receiveLua);  
        window.addEventListener('keydown', this.keyPress);
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        window.removeEventListener('keydown', this.keyPress);
    }
}
</script>

<style>
    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        list-style: none;

        font-family: 'Open Sans', sans-serif;
    }
    .MarketOpenned{
        display: none;
    }
    .NotifyOpenned{
        display: none !important;
    }
    .Notify{
        min-width: 103vh;
        height: 8vh;

        position: absolute;
        top: 16%;
        left: 50%;
        transform: translate(-50%, -16%);

        color: #ffffff;
        
        z-index: 1;

        font-size: 2.5vh;
        font-weight: bold;
        opacity: 0.95;

        padding: 2vh;

        display: flex;
        justify-content: flex-start;
        align-items: center;

    }
    .BodyBox{
        width: 103vh;
        height: 70vh;

        border-radius: 5px;
        overflow: hidden;
        /* border-bottom: 2px solid black; */

        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    .HeaderBox{
        display: flex;
        flex-direction: row;
        align-items: center;

        background-color: #536175;
        padding: 2.2vh;
    }

    .Title{
        flex: 1;
        font-size: 3.5vh;
        font-weight: bold;
    }
    .SubTitle{
        flex: 2;
        text-align: center;

        font-size: 2.2vh;
        font-weight: 600;
    }

    .CloseTitle{
        text-align: center; 

        font-size: 2.2vh;
        font-weight: bold;
        opacity: 0.8;
    }

    .Title, .SubTitle, .CloseTitle{
        color: #E3E6EA;
    }

    .SectionBox{
        width: 100%;
        height: 100%;

        display: flex;
        flex-direction: row;

        /* overflow: hidden; */
    }

    .OptionsBox{
        width: 27%;
        height: 100%;
        background-color: #3A3D4D;

        padding: 2vh;


        position: relative;
    }

    .OptionsText{
        font-size: 2vh;
        font-weight: 700;
        color: #6C7083;

        margin-bottom: 2vh;
    }

    .ItemBox{
        position: relative;
        display: flex;
        flex-direction: row;

        align-items: center;
        justify-content: center;

        border-bottom: 1px solid #707070;

        /* background-color: black; */

        padding: 1vh 0vh;
        /* margin-top: 2vh; */
    }
    .clickOption{
        position: absolute;
        width: 100%;
        height: 100%;

        /* background-color: black; */
        border-radius: 5px;
    }

    .clickOption:active{
        background-color: rgba(0, 0, 0, 0.145);
    }

    .ItemTextBox{
        flex: 1;

        margin-left: 1vh;
        /* margin-top: 2vh; */
    }

    .ItemTitle{
        color: #E3E6EA;
        font-size: 2.0vh;
    }

    .ItemText{
        color: #6C7083;
        font-size: 1.6vh;
        /* margin-bottom: 1vh; */
    }

    .ItemImageBox{
        width: 13%;
        height: 100%;

        display: flex;
        flex-direction: row;

        align-items: center;
        justify-content: center;
    }

    .ItemImageBox img{
        width: 100%;
    }

    .CircleGreen{
        background-color: #0FDE46;

        width: 1.2vh;
        height: 1.2vh;
        border-radius: 50%;
    }

    .CircleRed{
        background-color: #EC1B23;

        width: 1.2vh;
        height: 1.2vh;
        border-radius: 50%;
    }

    .ContentBox{
        flex: 1;
        background-color: #E3E6EA;
        height: 100%;

        position: relative;
    }

    .loader {
        border: 16px solid #f3f3f3; /* Light grey */
        
        border-radius: 50%;
        width: 20vh;
        height: 20vh;
        animation: spin 2s linear infinite;

        margin: auto;
        margin-top: 15vh;
    }

    .color-red{
        border-top: 16px solid #db3434; /* red */
    }

    .color-green{
        border-top: 16px solid #06a020; /* green */
    }

    @keyframes spin {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
    }
</style>