<template>
    <div class="AnunciosBox">
        <div class="loader color-red" v-if="isLoading"></div>

        <div v-else>
            <div class="SearchBox" v-if="!inProduct">
                <img src="../assets/search.png" class="SearchImage">
                <input type="text" class="SearchInput" v-model="search" :placeholder="translate.TR_SEARCH">
            </div>

            <div class="ProductsBox" v-if="!inProduct">
                <p class="ButtonRefresh" v-on:click="DisplayModal">{{translate.TR_BUTTON_ANNOUNCE}}</p>
                <ul class="ProductsList">
                    <li class="ProductsItemBox"
                        v-for="(item,index) in myProducts.filter((item) => item.name.toLowerCase().includes(search.toLowerCase()))"
                        :key="index"
                    >
                        <div class="DeleteBox">
                            <img v-on:click="removeItem(item)" src="img/delete.png" alt="" class="DeleteImage">
                            <p class="DeleteText">{{translate.TR_BUTTON_REMOVE}}</p>
                        </div>
                        <p class="ProductsItemName">{{item.name}} x{{item.amount}}</p>

                        <img :src="item.img" alt="" class="ProductsItemImage">
                        
                        <p class="ProductsItemOwner">{{translate.TR_BY_OWNER}} {{item.owner}}</p>

                        <p class="ProductsItemPrice">{{translate.TR_SIMBOL_MONEY}} {{item.price}}</p>
                    </li>
                </ul>
            </div>

            <div class="modalBox" v-if="modalOpenned">
                <p class="modalTitle">{{translate.TR_MODAL_TITLE}}</p>
                <div class="formBox">
                    <div class="cellBox">
                        <label for="cellItem">{{translate.TR_MODAL_ITEM}}</label>
                        <select name="" id="cellItem" v-model="selectedItem"
                            v-on:change="changePrice()"
                        >
                            <option
                            v-for="(item,index) in inventory"
                            :key="index"
                            :value="item"                            
                            >{{item.name}}</option>
                        </select>
                    </div>

                    <div class="cellBox">
                        <label for="cellAmount">{{translate.TR_MODAL_AMOUNT}}</label>
                        <input type="number" min="0" step="1" v-model="amountItem" class="cellItem" id="cellAmount">
                    </div>
                    <div class="cellBox">
                        <label for="cellPrice">{{translate.TR_MODAL_PRICE}}</label>
                        <input type="number" min="0" v-model="priceItem" class="cellItem" id="cellPrice">
                    </div>

                    

                    <div class="cellCheckBox">
                        <label for="cellAnonymous">{{translate.TR_MODAL_ANONYMOUS}}</label>
                        <input type="checkbox" id="cellAnonymous" v-model="anonymous" class="cellAnonymous" placeholder="Anonymous">
                    </div>

                    <div class="formButtonBox">
                        <p class="NeedsButtonBuy" v-on:click="anunciarItem">{{translate.TR_BUTTON_ANNOUNCE}}</p>
                        <p class="NeedsButtonCancel" v-on:click="DisplayModal(false)">{{translate.TR_BUTTON_CANCEL}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        myProducts:{

        },

        inventory:{

        },
        translate:{

        },
        NameResource:{

        }
    },

    data: function(){
        return({
            search: "",

            modalOpenned: false,

            selectedItem: "",
            amountItem: 1,
            priceItem: 0,
            anonymous: false,
            isLoading: true,
        })
    },

    methods:{
        changePrice: function(){
            this.priceItem = this.selectedItem.price_recommended;
            this.amountItem = 1;
        },

        anunciarItem: function(){
            if(!this.selectedItem.name || !this.amountItem || !this.priceItem)
                return;

            if(this.selectedItem.amount >= this.amountItem && this.amountItem > 0 && Number(this.priceItem) >= 0 && Number(this.priceItem) <= 999999999 ){
                fetch(`https://${this.NameResource}/anunciarItem`, {
                    method: 'POST',
                    body: JSON.stringify({
                        item: {
                            name: this.selectedItem.name,
                            weight: this.selectedItem.weight,
                            amount: this.amountItem,
                            price: this.priceItem
                        },
                        anonymous: this.anonymous
                    })
                }); 

                this.DisplayModal(false);
                this.isLoading = true;
            }
            else{
                fetch(`https://${this.NameResource}/sendNotify`, {
                    method: 'POST',
                    body: JSON.stringify({
                       color: "brown",
                       Notify: this.translate.TR_DONT_AMOUNT2
                    })
                }); 
            }
        },

        DisplayModal: function(value){
            this.modalOpenned = value;
        },

        removeItem: function(item){
            fetch(`https://${this.NameResource}/removeItem`, {
                method: 'POST',
                body: JSON.stringify({
                    item: item
                })
            }); 

            this.isLoading = true;
        },

        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.myProductsOpen){
                this.isLoading = false;

                // this.inventory = lua.inventory;
            }
            else if(lua.Refused){
                this.isLoading = false;
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receiveLua);  
        // window.addEventListener('keydown', this.keyPress);
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        // window.removeEventListener('keydown', this.keyPress);
    }
}
</script>

<style>
    .modalBox{
        position: absolute;
        top: 20%;
        left: 50%;

        transform: translate(-50%, -20%);

        background-color: white;

        width: 80%;
        height: 80%;

        border-radius: 10px;
        padding: 2vh;

        overflow: hidden;

        box-shadow: 1px 1px 10px 1px gray;

        animation: entrada;
        animation-duration: 0.4s;
    }

    @keyframes entrada{
        0%{ top: 110%; }
        100%{ top: 20%;}
    }

    .modalTitle{
        font-size: 3vh;
        font-weight: bold;
        color: #606060;

        text-align: center;
    }

    .cellBox{
        display: flex;
        flex-direction: column;

        margin-bottom: 1vh;
    }

    .cellBox label{
        font-size: 2.4vh;

        color: #606060;
    }

    .cellBox select, .cellBox input{
        border: 1px solid gray;

        border-radius: 5px;

        font-size: 2.4vh;
        padding: 0vh 0.5vh;
        color: #606060;

        outline: none;
    }

    .cellCheckBox{
        display: flex;
        align-items: center;
    }
    .cellCheckBox label{
        font-size: 2vh;
        margin-right: 1vh;

        color: #606060;
    }

    .formButtonBox{
        width: 12vh;
        margin:auto;
        text-align: center;
    }

    .ProductsItemBox:hover .DeleteBox{
        display: flex;
        
    }
    .DeleteBox{
        width: 100%;
        height: 100%;

        background-color: rgba(0, 0, 0, 0.782);
        /* opacity: 0.78; */

        display: none;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        position: absolute;
        top: 0%;
    }

    .DeleteImage{
        width: 30%;
    }

    .DeleteText{
        color:#D6DADF;
        font-size: 2vh;
        margin-top: 1vh;
        font-weight: bold;
    }
</style>